/* eslint-disable eqeqeq */
import { useEffect, useReducer } from "react";
import { Button } from 'antd'

//componets
import AnimatedBackground from "./components/particles";
import Cards from "./components/card";
import Page from "./components/pageView";
//Syles

import './styles/themeLight.less'
import './styles/themeDark.less'
import './App.css'

import {MenuProvider} from './Context/menu'


type typeVariables = {
  theme:string,
  themeBack:string,
  isLight:boolean
}

const InicialValuesVar = {
  theme: 'light',
  themeBack: 'lightBack',
  isLight:true
}

type functionsDeclaretions = 
|{ type:"changeTheme",valTheme:string} 
|{  type:"changeThemeBack",valTheme:string}
|{  type:"alterTheme",valTheme:boolean}

function reducer (state:typeVariables , action: functionsDeclaretions){
  switch (action.type) {
    case 'changeTheme':
      return{
        ...state,
        theme : action.valTheme
      }
    case 'changeThemeBack':
      return{
        ...state,
        themeBack : action.valTheme
      }
    case 'alterTheme':
      return{
        ...state,
        isLight:action.valTheme
      }
  }
}
const App: React.FC = () =>  {
  const [variables,handleFuntions] = useReducer(reducer,InicialValuesVar)
 
  useEffect(() => {
   
    if(variables.isLight){
      document.body.classList.remove('dark');
      document.body.classList.add('light');
    }else{
      document.body.classList.remove('light');
      document.body.classList.add('dark');
    }
   
  },[variables.isLight]); 
 

  function changeTheme(){
    if(variables.isLight){
      handleFuntions({
        type:'alterTheme',
        valTheme:false
      })
    }else{
      handleFuntions({
        type:'alterTheme',
        valTheme:true
      })
    }
  /* if(variables.theme == 'light'){
    handleFuntions({type:'changeTheme',valTheme:"dark"})
    handleFuntions({type:'changeThemeBack',valTheme:"darkBack"})
   }else{
    handleFuntions({type:'changeTheme',valTheme:"light"})
    handleFuntions({type:'changeThemeBack',valTheme:"lightBack"})
   }*/
  }
  // <!--<Button type='primary' onClick={()=>changeTheme()}>Trocar tema</Button>-->
  return (
 
    <div className={variables.isLight?"main" :"main dark"}>
      <div className={variables.isLight?"lightBack" :"darkBack"}>
      <AnimatedBackground theme={variables.isLight}/>
      </div>
        <div  className={"content"}> 
          <MenuProvider>
            <Cards theme={variables.isLight}/>
            <Page/> 
          </MenuProvider>
        </div>
 
      <Button type='primary' style={{zIndex:15}} onClick={()=>changeTheme()}>Trocar tema</Button>
    </div>

  )
}
export default App