/* eslint-disable jsx-a11y/anchor-is-valid */
import Icon from '@ant-design/icons';
import './menu.css'
//contexto

import {MenuContext} from '../../Context/menu'
import { useContext } from 'react';

const userIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="1em" height="1em" fill="currentColor" viewBox="0 0 16 16"> <path d="M8 8a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm2-3a2 2 0 1 1-4 0 2 2 0 0 1 4 0zm4 8c0 1-1 1-1 1H3s-1 0-1-1 1-4 6-4 6 3 6 4zm-1-.004c-.001-.246-.154-.986-.832-1.664C11.516 10.68 10.289 10 8 10c-2.29 0-3.516.68-4.168 1.332-.678.678-.83 1.418-.832 1.664h10z"/> </svg>
)
const chatIconCode = () =>(
  <svg xmlns="http://www.w3.org/2000/svg" width="1em" height="1em" fill="currentColor" viewBox="0 0 16 16"> <path d="M14 1a1 1 0 0 1 1 1v8a1 1 0 0 1-1 1h-2.5a2 2 0 0 0-1.6.8L8 14.333 6.1 11.8a2 2 0 0 0-1.6-.8H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1h12zM2 0a2 2 0 0 0-2 2v8a2 2 0 0 0 2 2h2.5a1 1 0 0 1 .8.4l1.9 2.533a1 1 0 0 0 1.6 0l1.9-2.533a1 1 0 0 1 .8-.4H14a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2z"/> <path d="M3 3.5a.5.5 0 0 1 .5-.5h9a.5.5 0 0 1 0 1h-9a.5.5 0 0 1-.5-.5zM3 6a.5.5 0 0 1 .5-.5h9a.5.5 0 0 1 0 1h-9A.5.5 0 0 1 3 6zm0 2.5a.5.5 0 0 1 .5-.5h5a.5.5 0 0 1 0 1h-5a.5.5 0 0 1-.5-.5z"/> </svg>
)
const wordksIconCode = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="1em" height="1em" fill="currentColor"  viewBox="0 0 16 16"> <path d="M10.478 1.647a.5.5 0 1 0-.956-.294l-4 13a.5.5 0 0 0 .956.294l4-13zM4.854 4.146a.5.5 0 0 1 0 .708L1.707 8l3.147 3.146a.5.5 0 0 1-.708.708l-3.5-3.5a.5.5 0 0 1 0-.708l3.5-3.5a.5.5 0 0 1 .708 0zm6.292 0a.5.5 0 0 0 0 .708L14.293 8l-3.147 3.146a.5.5 0 0 0 .708.708l3.5-3.5a.5.5 0 0 0 0-.708l-3.5-3.5a.5.5 0 0 0-.708 0z"/> </svg>
)
const contactIconCode = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="1em" height="1em" fill="currentColor"  viewBox="0 0 16 16"> <path d="M13.106 7.222c0-2.967-2.249-5.032-5.482-5.032-3.35 0-5.646 2.318-5.646 5.702 0 3.493 2.235 5.708 5.762 5.708.862 0 1.689-.123 2.304-.335v-.862c-.43.199-1.354.328-2.29.328-2.926 0-4.813-1.88-4.813-4.798 0-2.844 1.921-4.881 4.594-4.881 2.735 0 4.608 1.688 4.608 4.156 0 1.682-.554 2.769-1.416 2.769-.492 0-.772-.28-.772-.76V5.206H8.923v.834h-.11c-.266-.595-.881-.964-1.6-.964-1.4 0-2.378 1.162-2.378 2.823 0 1.737.957 2.906 2.379 2.906.8 0 1.415-.39 1.709-1.087h.11c.081.67.703 1.148 1.503 1.148 1.572 0 2.57-1.415 2.57-3.643zm-7.177.704c0-1.197.54-1.907 1.456-1.907.93 0 1.524.738 1.524 1.907S8.308 9.84 7.371 9.84c-.895 0-1.442-.725-1.442-1.914z"/> </svg>
)
const ChatIcon = (props:any) => <Icon component={chatIconCode} {...props} />
const UserIcon = (props:any) => <Icon component={userIcon} {...props} />
const WorksIcon = (props:any) => <Icon component={wordksIconCode} {...props} />
const ContactIcon = (props:any) => <Icon component={contactIconCode} {...props} />


function Menu(props:{MenuName:string,theme:boolean}) {
  var first = props.MenuName.split(" ")[0]
  var second = props.MenuName.split(" ")[1]
  const {openMenu,isOpen,handleMenu} = useContext(MenuContext) 


  return (

    <div className="nav">
      <div className={props.theme?isOpen?'navTextActive lightTextOnly':'navText lightTextOnly':isOpen?'navTextActive darkTextOnly':'navText darkTextOnly' }>{first}</div>
      <div className='center'>
        <div className={props.theme?isOpen?'navMenuActive menulight':'navMenu menulight':isOpen?'navMenuActive menudark':'navMenu menudark'}>
          <input type="checkbox" onChange={(e)=>{openMenu(e.target.checked)}} className="toggler" />
          <div className={props.theme?"hamburger hamlight":"hamburger hamdark"}><div></div></div>
          <div className={props.theme?isOpen?'itensMenuActive light':'itensMenu dark':isOpen?'itensMenuActive dark':'itensMenu dark'}>
            <li className='itens'>
              <a href="#about" onClick={()=>handleMenu('about')}><UserIcon className="icon"/>SOBRE</a>
            </li>
            <li className='itens'>
              <a href="#resume" onClick={()=>handleMenu('resume')}><ChatIcon className="icon"/> RESUMO</a>
            </li>
            <li className='itens'>
              <a href="#" onClick={()=>handleMenu('works')}><WorksIcon className="icon"/>TRABALHOS</a>
            </li>
            <li className='itens'>
              <a href="#"  onClick={()=>handleMenu('contact')}><ContactIcon className="icon"/> CONTATO</a>
            </li>
          </div>
        </div>
      </div>
     
      <div className={props.theme?isOpen?'navTextActive lightTextOnly':'navText lightTextOnly':isOpen?'navTextActive darkTextOnly':'navText darkTextOnly' }>{second}</div>
     
    </div>
  
)}

export default Menu