/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable eqeqeq */
/* eslint-disable react-hooks/exhaustive-deps */
import { GithubOutlined, InstagramOutlined, LinkedinOutlined } from '@ant-design/icons';
import { Space } from 'antd';
import React, { useContext, useEffect, useLayoutEffect, useState } from 'react';
import Menu from '../menu';
//styles
import './card.css'
//context

import { MenuContext } from '../../Context/menu';

const linkedin = 'https://www.linkedin.com/in/marcus-vinicius-dos-santos-5ba156123/'
const gitbuh = 'https://github.com/engmarcus'
const instagram = 'https://www.instagram.com/marcusdjsbs/'


export default function Cards (props: { theme: boolean })  {
  const {handleDark} = useContext(MenuContext)
  useLayoutEffect(()=>{
    handleDark(!props.theme)
  },[props.theme])
  const [word,setWord] = useState([
    'Analista de TI',
    'Enginer Developer',
    'Font-End Developer',
    'Back-End Developer',
    
  ])
  const [sort,setsort] = useState("")
  const [next,setNext] = useState(false)
  const [possitionWord,setPossitionWord] = useState(0)


  function sleep(ms:number) {
    return new Promise((resolve) => {
      setTimeout(resolve, ms);
    });
  }
  function writeText(textWrite : string){
    let text = ''
    textWrite.split('').forEach( async (words,index) =>{
      sleep(90 * index).then(()=>{
        return  text = text.concat(words)
      }).then((a)=>{setsort(a)}).finally(()=> {});
    })
  }
  function deleteText(textDelete : string) {
    let size =  textDelete.split('').length
    let text = textDelete.split('')
    for (let index = 0; index <= size; index++) {
      sleep(50 * index).then(()=>{
          setsort(text.toString().replaceAll(',',''))
          text.pop()?.toString()
      })
    }
  }
  useEffect(()=>{
    play()
  },[])
  
  useEffect(()=>{
    if(possitionWord == 0){
      setPossitionWord(1)
      play()
    }else if(possitionWord == 1){
      setPossitionWord(2)
      play()
    }else if(possitionWord == 2){
      setPossitionWord(3)
      play()
    }else if(possitionWord == 3){
      setPossitionWord(0)
      play()
    }

  },[next])
  async function play() {
    writeText(word[possitionWord])
    await sleep(4500)
    deleteText(word[possitionWord])
    await sleep(2000)
    setNext(!next)
  }
  return(
    <div className={props.theme?"card light" :"card dark"} >
      <div className={props.theme?"cardImg light" :"cardImg dark"} > </div>
      <div className='cardInformation'>
        <Menu MenuName="Marcus Vinicius" theme={props.theme}/>
        <div className='autoText'>
          <div className={props.theme?"autolight" :"autodark"}>
            {sort}
          </div>
        </div>
        <div className='socialMedia'> 
          <Space >
            <a href={linkedin} target='blank'><LinkedinOutlined className='icons' /></a>
            <a href={gitbuh} target='blank'><GithubOutlined className='icons'/></a>
            <a href={instagram} target='blank'><InstagramOutlined className='icons'/></a>
          </Space>
        </div>
          <div className='links'>
            <a href='#' className={props.theme?"alight":'adark'}> Download CV</a>
            <a href='#' className={props.theme?"alight":'adark'}> Contato</a>
          </div>
      </div>
    </div>
  );
}
  

