/* eslint-disable eqeqeq */
import { useEffect, useState } from "react";
import Particles from "react-particles";
import { loadFull } from "tsparticles";
import { Engine } from 'tsparticles-engine';


export default function AnimatedBackground(props: { theme: boolean; }) {
  const[colorParticle,setColorparticle] = useState("#C0D9E5")
  const[colorLinks,setColorlinks] = useState("#DDF5F7")

  useEffect(()=>{

    if(props.theme){
      setColorparticle("#44679F")
      setColorlinks("#083358")
    }else{
      setColorparticle("#DDF5F7")
      setColorlinks("#FFD717")
    }
  },[props.theme])
 // - Light => #DDF5F7 #C0D9E5 #44679F #3B577D
 // - Dark  => #001F3F #083358 #0D63A5 #FFD717

  const particlesInit = async function particlesInit (engine: Engine) {
    //console.log(engine);
    return await loadFull(engine);
  };

  const particlesLoaded = async function particlesLoaded (container: any) {
    return container;
  };

  

  return(
    <Particles
      id="tsparticles"
      init={particlesInit}
      loaded={particlesLoaded}
      options={{
        fpsLimit: 120,
        backgroundMode:{
          enable:true,
          zIndex:0
        },
        /*interactivity: {
          events: {
            onClick: {
              enable: true,
              mode: "push",
            },
            onHover: {
              enable: true,
              mode: "grab",
            },
            resize: true,
          },
          modes: {
            grab: {
              distance: 140,
              line_linked: {
                opacity: 1
              }
            },
            bubble: {
              distance: 400,
              size: 40,
              duration: 2,
              opacity: 8,
              speed: 3
            },
            repulse: {
              distance: 200,
              duration: 0.8
            },
            push: {
              particles_nb: 4
            },
            remove: {
              particles_nb: 2
            }
          }
        },*/
        particles: {
          color: {
            value: colorParticle,
          },
          links: {
            color: colorLinks,
            distance: 125,
            enable: true,
            opacity: 0.5,
            width: 4,
            anim: {
              enable: false,
              speed: 20,
              opacity_min: 0.1,
              sync: false
            }
          },
          collisions: {
            enable: false,
            mode:"bounce"
          },
          move: {
            enable: true,
            speed: 2.2,
            direction: "none",
            random: true,
            straight: true,
            out_mode: "bounce",
            bounce: true,
            attract: {
              enable: false,
              rotateX: 1605,
              rotateY: 1210,
             
            }
          },
          number: {
            density: {
              enable: true,
              area: 999,
            },
            value: 95,
          },
          opacity: {
            value: 1,
            random: true,
            anim: {
              enable: true,
              speed: 2,
              opacity_min: 0.1,
              sync: false
            }
          },
          shape: {
            type: "circle",
            polygon: {
              nb_sides: 6,
            },
          },
          size: {
            value:3.5,
            random: false,
            anim: {
              enable: true,
              speed: 30,
              size_min: 0.1,
              sync: false
            }
          },
          random: true,  
        },
        detectRetina: true,
      }}
    />
  )

}