/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useEffect, useState } from "react";
import { createContext } from "react";

type MenuContextProps= {
  children:React.ReactNode
}

const inicialState ={
  isOpen: false,
  isDark:false,
  menuActive:'about',
  handleDark:(newState: boolean) =>{},
  openMenu: (stateMenu: boolean) => {},
  handleMenu:(nemMenu:string) =>{}
}
export const MenuContext = createContext(inicialState)

export const MenuProvider = ({children}:MenuContextProps) => {
  let [isOpen,setOpen]     = useState(inicialState.isOpen)
  let [isDark,setDark]     = useState(inicialState.isDark)
  let [menuActive,setMenu] = useState(inicialState.menuActive)
  function openMenu(stateMenu:boolean){
    setOpen(stateMenu)
  }
  function handleDark(dark:boolean){
    setDark(dark)
  }  
  function handleMenu(value:string){
    setMenu(value)
  }
  return(
    <MenuContext.Provider value={{isOpen,openMenu,isDark,handleDark,menuActive,handleMenu}}>
      {children}
    </MenuContext.Provider>
  );
}